<template>
    <H1>Add Timeline</H1>
    <input
            type="string"
            name="string"
            v-model="year"
            placeholder="year">
    <br>
    <input
            type="string"
            name="string"
            v-model="month"
            placeholder="month">
    <br>
    <input
            type="description"
            name="description"
            v-model="description"
            placeholder="description">
    <br>
    <button @click="timelineAdd">Submit</button>
</template>

<script>
    import AuthenticationService from "../services/TimelineAddService";
    export default {
        data(){
            return{
                year:'',
                month:'',
                description:''
            }
        },
        methods:{
            async timelineAdd(){
                const response = await AuthenticationService.timelineAdd({
                    year:this.year,
                    month: this.month,
                    description:this.description
                })
                console.log(response.data())
            }
        }
    }

</script>

